body {
  margin: 0;
  padding: 0;
  background: url("https://i.imgur.com/3pzRj9n.png");
  font-family: "Poppins", sans-serif;
  color: #f8f8f8;
  height: 100vh;
  overflow: hidden;
}


.overlay {
  position: fixed;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;

}


.App {
  width: 80%;
  max-width: 1300px;
  height: 80%;
  background: rgba(10, 10, 20, 0.92);
  border-radius: 20px;
  box-shadow: 0 0 60px rgba(0, 0, 0, 0.75);
  padding: 20px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}



.header-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 12px;

  margin-bottom: 24px;

  border-bottom: 1px solid rgba(255, 255, 255, 0.08);

}


.title-group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

h1 {
  margin: 0;
  font-size: 1.9rem;
  color: #e1d4ff;
  text-shadow: 0 0 10px rgba(138, 43, 226, 0.4);
  letter-spacing: 1px;
}

.subtitle {
  margin-top: 4px;
  font-size: 0.9rem;
  color: #b0aad0;
  letter-spacing: 1px;
}


.header-actions {
  display: flex;
  align-items: center;
  gap: 10px;
}


.icon-btn {
  background: rgba(255, 255, 255, 0.08);
  border: none;
  color: #ccc;
  width: 38px;
  height: 38px;
  border-radius: 10px;
  font-size: 1.1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background 0.3s ease, color 0.3s ease, transform 0.25s ease, box-shadow 0.25s ease;
}

.icon-btn:hover {
  background: rgba(255, 255, 255, 0.15);
  color: #fff;
  transform: scale(1.05);
  box-shadow: 0 0 8px rgba(138, 43, 226, 0.4);
}


.category-toggle {
  position: relative;
}

.category-dropdown {
  position: absolute;
  top: 45px;
  right: 0;
  background: rgba(20, 20, 35, 0.96);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  padding: 8px;
  width: 180px;
  max-height: 250px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  z-index: 100;
}


.category-dropdown::-webkit-scrollbar {
  width: 6px;
}

.category-dropdown::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #7a2aff, #4722a6);
  border-radius: 6px;
}

.category-dropdown::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
}



.category-dropdown button {
  background: transparent;
  border: none;
  color: #ccc;
  padding: 7px 10px;
  text-align: left;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.25s, color 0.25s;
}

.category-dropdown button:hover {
  background: rgba(138, 43, 226, 0.3);
  color: #fff;
}

.category-dropdown button.active {
  background: linear-gradient(135deg, #8a2be2, #4b1bab);
  color: #fff;
  font-weight: 600;
  box-shadow: 0 0 8px rgba(138, 43, 226, 0.5);
}


.search-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  height: 38px;
  transition: width 0.3s ease, background 0.3s ease;
  width: 38px;
}

.search-icon {
  color: #ccc;
  font-size: 1rem;
  margin: 0 10px;
  cursor: pointer;
  flex-shrink: 0;
  transition: color 0.3s ease;
}

.search-icon:hover {
  color: #fff;
}

.search-wrapper input {
  background: transparent;
  border: none;
  color: #fff;
  font-size: 0.8rem;
  width: 0;
  opacity: 0;
  transition: width 0.3s ease, opacity 0.3s ease;
  outline: none;
}

.search-wrapper:hover,
.search-wrapper:focus-within {
  width: 200px;
  background: rgba(255, 255, 255, 0.12);
  box-shadow: 0 0 8px rgba(138, 43, 226, 0.4);
}

.search-wrapper:hover input,
.search-wrapper:focus-within input {
  width: 100%;
  opacity: 1;
}


.content-wrapper {
  flex: 1;
  display: flex;
  gap: 20px;
  height: 100%;
  overflow: hidden;
}


.vehicle-grid {
  flex: 2;
  overflow-y: auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 20px;
  padding: 8px;
  scrollbar-width: thin;
  scrollbar-color: #6a1cc5 transparent;
}

.vehicle-grid::-webkit-scrollbar {
  width: 8px;
}

.vehicle-grid::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #7a2aff, #4722a6);
  border-radius: 10px;
}


.vehicle-info {
  flex: 1;
  background: rgba(20, 20, 30, 0.9);
  border-radius: 15px;
  padding: 20px;
  display: flex;
  max-height: 90%;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.6);
  margin-top: 10px;
}

.vehicle-info h2 {
  text-align: center;
  color: #e1dfff;
  text-shadow: 0 0 5px rgba(138, 43, 226, 0.4);
  margin-top: 0;
}

.vehicle-image-container {
  text-align: center;
  margin: 10px 0 15px 0;
}

.vehicle-image-container .fallback-icon {
  font-size: 80px;
  color: #8a2be2;
  opacity: 0.8;
}

.vehicle-image-container img {
  width: 100%;
  max-height: 160px;
  object-fit: contain;
}

.vehicle-details {
  margin: 10px 0;
  font-size: 0.9rem;
  line-height: 1.4rem;
  color: #cfcfcf;
}

.vehicle-details b {
  color: #fff;
}

.no-selection {
  text-align: center;
  color: #888;
  margin: auto;
  font-style: italic;
}


.spawn-actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  margin-top: 10px;
}


.spawn-btn {
  flex: 1;
  height: 48px;
  background: linear-gradient(135deg, #8a2be2, #4b1bab);
  border: none;
  color: #fff;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.spawn-btn:hover {
  background: linear-gradient(135deg, #a866ff, #5a2ed6);
  box-shadow: 0 0 12px rgba(138, 43, 226, 0.6);
}

.color-picker-wrapper {
  position: relative;
}

.palette-btn {
  width: 48px;
  height: 48px;
  background: linear-gradient(135deg, #8a2be2, #4b1bab);
  border: none;
  color: #fff;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.palette-btn:hover {
  background: linear-gradient(135deg, #a866ff, #5a2ed6);
  box-shadow: 0 0 12px rgba(138, 43, 226, 0.6);
}


.color-popover {
  position: absolute;
  bottom: 58px;
  right: 0;
  z-index: 200;
  background: rgba(20, 20, 35, 0.95);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.6), 0 0 10px rgba(138, 43, 226, 0.4);
  padding: 12px;
}


.color-popover .react-colorful {
  width: 160px;
  height: 120px;
}

.color-popover .react-colorful__saturation {
  border-radius: 8px;
}

.color-popover .react-colorful__hue {
  border-radius: 8px;
  margin-top: 8px;
}

.vehicle-card {
  background: rgba(15, 15, 25, 0.95);
  border-radius: 12px;
  padding: 10px 8px 8px 8px;
  text-align: center;
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.07);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 180px;
  height: 160px;
  box-sizing: border-box;
  transition: box-shadow 0.2s ease, border 0.2s ease, transform 0.15s ease;
}

.vehicle-card:hover {
  border: 1px solid #8a2be2;
  box-shadow: 0 0 20px rgba(138, 43, 226, 0.5);
  transform: translateY(-2px);
}


.vehicle-card img {
  height: 100px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 10px;
  display: block;
}


.vehicle-card .fallback-icon {
  height: 80px;
  width: auto;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #8a2be2;
  opacity: 0.8;
  font-size: 28px;
}

.vehicle-stats {
  margin-top: 10px;
  margin-bottom: 15px;
}

.vehicle-stats h3 {
  text-align: center;
  font-size: 1.1rem;
  color: #ccc;
  margin-bottom: 10px;
}

.stat-bar {
  margin: 8px 0;
}

.stat-label {
  display: flex;
  justify-content: space-between;
  font-size: 0.85rem;
  margin-bottom: 3px;
  color: #bbb;
}

.stat-container {
  position: relative;
  background: rgba(255, 255, 255, 0.08);
  border-radius: 6px;
  height: 8px;
  overflow: hidden;
}

.stat-fill {
  height: 100%;
  border-radius: 6px;
  transition: width 0.3s ease;
}

.stat-value {
  color: #aaa;
}


.loader-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #cfc1ff;
  text-align: center;
  gap: 10px;
}

.loader-icon {
  font-size: 2.5rem;
  color: #8a2be2;
  text-shadow: 0 0 15px rgba(138, 43, 226, 0.6);
}


.footer-bar {
  width: 100%;
  transform: translateY(20%);
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 14px;
}

.page-btn {
  background: rgba(255, 255, 255, 0.08);
  color: #ddd;
  border: none;
  border-radius: 8px;
  padding: 6px 14px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 0.9rem;
}

.page-btn:hover:not(:disabled) {
  background: rgba(138, 43, 226, 0.4);
  color: #fff;
}

.page-btn:disabled {
  opacity: 0.4;
  cursor: default;
}

.page-info {
  color: #bbb;
  font-size: 0.9rem;
}